@color-accent: #63d1bb;
@color-dark: #000000;

@tablet-width: 768px;
@desktop-width: 1200px;
@mobile-width-only: 767px;

.normalize-list() {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.normalize-link() {
  font-weight: 700;
  text-decoration: none;
  color: @color-dark;
}

.normalize-title() {
  margin: 0;
  padding: 0;
}

.visually-hidden() {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
