.catalog {
  padding: 30px;
  text-align: center;

  @media (min-width: @tablet-width) {
    padding-right: 50px;
    padding-left: 50px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 57px;
  }
}

.catalog__list {
  .normalize-list();

  @media (min-width: @desktop-width) {
    display: grid;
    grid-template-columns: repeat(3, 316px);
    gap: 51px;
  }
}

.catalog__item {
  margin-bottom: 30px;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, auto);
  }

  @media (min-width: @desktop-width) {
    display: block;
    margin-bottom: 0;
  }
}

.catalog-image__wrapper {
  @media (min-width: @tablet-width) {
    width: 50%;
  }
}

.catalog__item-link {
  font-size: 17px;
  .normalize-link();

  @media (min-width: @tablet-width) {
    display: flex;
    grid-area: 1 / 1 / 3 / 3;
  }

  @media (min-width: @desktop-width) {
    display: block;
  }
}

.catalog__item-title {
  .normalize-title();
  text-align: left;
  margin-top: 28px;
  margin-left: 28px;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    margin-top: 27px;
    margin-left: 33px;
  }

  @media (min-width: @desktop-width) {
    margin-left: 28px;
  }
}

.catalog__item-description {
  .normalize-title();
  font-size: 13px;
  line-height: 30px;
  text-align: left;
  margin-right: 28px;
  margin-left: 28px;
  padding-bottom: 18px;
  border-bottom: 1px solid #ececec;

  @media (min-width: @tablet-width) {
    font-size: 15px;
    margin-top: 58px;
    margin-left: 33px;
    border: none;
    grid-area: 1 / 2 / 2 / 3;
  }

  @media (min-width: @desktop-width) {
    margin-top: 6px;
    margin-left: 28px;
    padding-bottom: 37px;
    border-bottom: 1px solid #ececec;
  }
}

.catalog__cart {
  text-align: right;
  border: none;
  padding-top: 0;
  padding-bottom: 0;
}

.catalog__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 22px 27px 27px 28px;
  border-bottom: 1px solid #ececec;

  @media (min-width: @tablet-width) {
    padding: 24px 33px 26px 33px;
    height: 73px;
    align-self: end;
    border-top: 1px solid #ececec;
    grid-area: 2 / 2 / 3 / 3;
  }

  @media (min-width: @desktop-width) {
    border-top: none;
  }
}

.catalog__price {
  @media (min-width: @tablet-width) {
    font-size: 15px;
  }
}
